<template>
    <div>
        <van-nav-bar title="产品详情" left-text="返回" left-arrow @click-left="onClickLeft"/>
        <div class="product-group">
            <img v-for="(item,index) in imgList"  :key="index" width="375" v-lazy="item" alt="产品图"/>
        </div>
    </div>
</template>
<script>
import { NavBar } from "vant";
export default {
  components: {
    [NavBar.name]: NavBar
  },
  computed: {
    imgList() {
      var list = [];
      for (let i = 1; i <= 19; i++) {
        let imgItem = require(`../assets/images/img_${i}.png`);
        list.push(imgItem);
      }
      return list;
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back();
    }
  }
};
</script>
<style scoped>
.product-group {
  height: calc(100% - 50px);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow: auto;
}
</style>



